@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@300;400;500;600&display=swap");

body {
  margin: 0 auto;
  font-family: "Poppins", sans-serif;
}

#header {
  width: 100%;
  position: fixed;
  border-bottom: 1px solid gray;
  background-color: white;
  top: 0;
  z-index: 1000;
}

h1,
h2,
h3,
p {
  color: black;
}

.border {
  border: 2px solid black;
  border-radius: 2rem;
}

/* BUTTONS */

.btn-container {
  display: flex;
  justify-content: center;
  gap: 1rem;
}

.btn {
  font-weight: 600;
  transition: all 300ms ease;
  padding: 1rem;
  width: 8rem;
  border-radius: 2rem;
}

.btn-color-1,
.btn-color-2 {
  border: rgb(53, 53, 53) 0.1rem solid;
}

.btn-color-1:hover,
.btn-color-2:hover {
  cursor: pointer;
}

.btn-color-1,
.btn-color-2:hover {
  background: rgb(53, 53, 53);
  color: white;
}

.btn-color-1:hover {
  background: rgb(0, 0, 0);
}

.btn-color-2 {
  background: none;
}

.btn-color-2:hover {
  border: rgb(255, 255, 255) 0.1rem solid;
}

.btn-container {
  gap: 1rem;
}

html {
  scroll-behavior: smooth;
}

h1 {
  font-size: 5rem;
  font-family: "DM Sans", sans-serif;
  font-weight: 400;
  margin-bottom: 0;
}

section {
  padding-top: 100px;
}

nav,
.nav-links {
  display: flex;
  align-items: center;
  justify-content: space-around;
  list-style: none;
  text-decoration: none;
}

.nav-links {
  gap: 30px;
  font-size: 1.5rem;
  text-decoration: none;
}

.navbar-burger {
  width: 40px;
  height: 40px;
  display: none;
  border: none;
  background: transparent;
}

.flex {
  display: flex;
  justify-content: center;
  align-items: center;
}

@media screen and (max-width: 768px) {
  .nav-links {
    flex-direction: column;
    position: fixed;
    bottom: 0;
    height: 100vh;
    right: -150vw;
    visibility: hidden;

    gap: 60px;
    background-color: white;
    margin: 0;
  }

  .show-nav .nav-links {
    right: 0;
    width: 100vw;
    visibility: visible;
    transition: all 0.5s ease-out;
  }

  .navbar-burger {
    display: block;
    right: 1rem;
    top: 1rem;
  }

  .navbar-burger:hover {
    cursor: pointer;
  }

  .burger-bar,
  .burger-bar::before,
  .burger-bar::after {
    display: block;
    width: 40px;
    height: 3px;
    position: relative;
    border-radius: 3px;
    background-color: black;
    transition: all 0.5s ease;
  }
  .burger-bar::before,
  .burger-bar::after {
    content: "";
    position: absolute;
    left: 0;
  }

  .burger-bar::before {
    transform: translateY(-12px);
  }

  .burger-bar::after {
    transform: translateY(12px);
  }

  .show-nav .burger-bar {
    background: transparent;
  }

  .show-nav .burger-bar::before {
    transform: rotate(45deg);
  }
  .show-nav .burger-bar::after {
    transform: rotate(-45deg);
  }
}

a,
.btn {
  text-decoration: none;
  color: black;
  transition: all 300ms ease;
}

a:hover {
  text-decoration: underline;
  text-underline-offset: 0.5rem;
  color: grey;
  transform: scale(1.2);
}

.description {
  gap: 50px;
  padding-bottom: 100px;
  padding-top: 210px;
  width: 100%;
}

#profil{
  margin-bottom: 100px;
}

@media screen and (max-width: 768px) {
  .profile-details {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-left: 50px;
    margin-right: 50px;
  }
}

@media screen and (max-width: 768px) {
  .description {
    gap: 50px;
    width: 100%;
    flex-direction: column-reverse;
    padding-top: 100px;
    padding-bottom: 100px;
  }
}

.photo {
  width: 250px;
  border-radius: 50%;
  object-fit: cover;
  z-index: 1;
  animation-name: floating;
  animation-duration: 3s;
  animation-iteration-count: infinite;
  animation-timing-function: ease-in-out;
}

@keyframes floating {
  0% {
    transform: translate(0, 0px);
  }
  50% {
    transform: translate(0, 10px);
  }
  100% {
    transform: translate(0, -0px);
  }
}

.socials {
  gap: 20px;
}

.logo {
  width: 40px;
}

.kasa {
  width: 30%;
}

@media screen and (max-width: 768px) {
  .kasa {
    width: 70%;
  }
}

//Projects

.portfolio-section {
  flex-direction: column;
  gap: 50px;
}

.project-container {
  display: flex;
  justify-content: space-around;
  align-items: center;
  flex-direction: row;
  width: 40%;
  padding: 4rem;
  gap: 20px;
  height: auto;
  background-color: white;
}

.project-container:hover {
  transform: scale(1.02); /* Légère augmentation de la taille au survol */
}

.project-img {
  width: 100%;
  height: auto;
  border-radius: 1rem;
  border: 1px solid black;
}

.project-details {
  flex-direction: column;
  display: flex;
  align-items: center;
  gap: 20px;
}

.project-links {
  gap: 25px;
}

.bar {
  width: 1.5px;
  height: 40px;
  background-color: black;
}

.logo-grimoire {
  width: 100%;
}

.icon {
  width: 50px;
}

.row {
  flex-direction: row;
}

.column {
  flex-direction: column;
}

.experience-container {
  width: 20%;
  padding: 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

@media screen and (max-width: 950px) {
  .experience-container {
    width: 50%;
    padding: 2rem;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
