}

.exp {
  display: flex;
  justify-content: center;
  width: 100%;
  gap: 50px;
  margin-bottom: 100px;
  margin-top: 50px;
}

@media screen and (max-width: 950px) {
  .exp {
    flex-direction: column;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}

.experience-skills {
  display: flex;
  width: 100%;
  align-items: center;
  gap: 30px;
}

//About section

.about {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  border: 2px solid black;
  border-radius: 2rem;
  padding: 2rem;
}

.about-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 50px;
  width: 100%;
}

#about {
  display: flex;
  justify-content: center;
  align-items: center;
}

.about-section {
  width: 50%;
  gap: 50px;
}

//Contact

.contact-container {
  flex-direction: column;
  width: 100%;
  height: auto;
  margin-bottom: 50px;
}

.contact-details {
  gap: 20px;
  padding: 1.5rem;
  margin-top: 50px;
}

@media screen and (max-width: 500px) {
  .contact-details {
    flex-direction: column;
    gap: 0px;
  }
}

.input {
